<template>
  <div id="app">
    <template>
      <el-table
        :data="result.data"
        style="width: 100%"
        height="500">
        <el-table-column
          fixed
          prop="Address"
          label="地址"
          width="150"
        ></el-table-column>
        <el-table-column
          prop="Description"
          label="描述"
          width="120"
        ></el-table-column>
        <el-table-column
          prop="RestaurantName"
          label="名称"
          width="120"
        ></el-table-column>
        <el-table-column
          prop="TelephoneNumber"
          label="联系电话"
          width="120"
        ></el-table-column>
        <el-table-column
          label="已审查"
          width="120"
        >
          <template slot-scope="scope">
            <div v-if = "scope.row.isReviewed"> 是 </div>
            <div v-else> 否 </div>
          </template>

        </el-table-column>
        <el-table-column
          width="120"
        >
          <template slot-scope="scope">
            <el-button  v-if = "scope.row.isReviewed" v-on:click="onReviewChange(false, scope.row._id)" size="small" :loading="loading"> 取消审查 </el-button>
            <el-button  v-else v-on:click="onReviewChange(true, scope.row._id) " size="small" :loading="loading"> 确认审查 </el-button>
          </template>
        </el-table-column>
      </el-table>
    </template>
  </div>
</template>

<script>
  import axios from 'axios';
  export default {
    name: 'App',
    data() {
      return {
        appid: 'wx1b73f99b71b71538',
        appSecret: '8040e17bf84df69ae30e08bfe9721d8e',
        envid: "cloud-database-5hfz6",
        tokenUrl: '',
        queryUrl: '',
        accessToken: '',
        result: {
          data: [{
            Address: 'default',
            Description: 'default',
            RestaurantName: 'default',
            TelephoneNumber: 'default',
            isReviewed: true
          }]
        },
        loading: false
      };
    }
      ,
    mounted () {
      this.tokenUrl = '/cgi-bin/token?grant_type=client_credential&appid=' + this.appid + '&secret=' + this.appSecret
      let queryFuction = "db.collection(\"Restaurant\").where({}).get()"
      let upateFunction = "db.collection(\"Restaurant\").where({TelephoneNumber:\"110\"}).update({data:{TelephoneNumber :\"111\"}})"
      //this.query(queryFuction)
      //console.log(this.testjson.data[0].name)
      //this.do(queryFuction,this.query)
      //this.do(upateFunction,this.update)
      this.load()
    },
    methods: {
      /*
        action : database query string
        callback : database query type
      */
      do: function (action,callback) {
        axios.get(this.tokenUrl).then((res) => {
          console.log('do:', res.data)
          this.accessToken = res.data.access_token
          console.log('do:', this.accessToken)
          callback(this.envid,this.accessToken,action)
        }, function () {
          console.log('Unable to get token.')
        })
      },

      /*
        que
      */
      query: function (envid,accessToken,queryFuction) {
        console.log(this.accessToken)
        this.queryUrl = '/api/tcb/databasequery?access_token=' + accessToken
        let postdata = {"env":envid,"query":queryFuction}
        console.log(postdata)
        axios.post(this.queryUrl, postdata).then((res) => {
          console.log('query success')
          this.result = res.data
          for(var d in this.result.data){
            this.result.data[d] = JSON.parse(this.result.data[d])
          }
          console.log(this.result.data)
        }, function () {
          console.log('Query Failed')
        })
      },

      update: function (envid,accessToken,queryFuction) {
        console.log(this.accessToken)
        this.queryUrl = '/api/tcb/databaseupdate?access_token=' + accessToken
        let postdata = {"env":envid,"query":queryFuction}
        console.log(postdata)
        axios.post(this.queryUrl, postdata).then((res) => {
          console.log(res.data)
        }, function () {
          console.log('update Failed')
        })
      },

      load: function () {
        axios.get('http://111.231.170.248:8082/restaurants').then((res) => {
          this.result = res.data
          for(var d in this.result.data){
            this.result.data[d] = JSON.parse(this.result.data[d])
          }
          console.log(this.result.data)

        })

      },
      onReviewChange: function (type, id) {
        this.loading = true
        let postdata = {'reviewed': type, 'id': id}
        axios.post('http://111.231.170.248:8082/updateReview', postdata).then((res) => {
          console.log(res.data)
          this.load()
          this.loading = false
        }, function () {
          console.log('update Failed')
        })
      }

    }

  }
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



